<template>
    <div>
        <h2>Изменение комплектации</h2>
        <form class="form" action="." method="post" accept-charset="utf-8">
            <div id="tab" class="demo">

				<label for='name'>Наименование</label>
				<input v-model="name" id='name' class="form-control" name='name' type='text'/>

				<label for='poster'>Изображение</label>
				<div class="wrap-poster">
					<a :href="this.$store.state.domain + '/' + path + 'pickings.' + img_format" target="_blank">
						<img :src="this.$store.state.domain + '/' + path + 'pickings.' + img_format" class="activePoster">
					</a>
					<div class="inputPoster">
						<input ref="poster" @change="posterFiles" type="file" name="poster" accept="image/jpeg,image/png" hidden />
						<div class="image-name">{{ poster_name }}</div>
						<div class="select-image" @click="$refs.poster.click()">Выбрать</div>
					</div>
				</div>

                <div v-if="statusm == 0" class="message">{{ message }}</div>
                <div v-else-if="statusm == -1" class="message alert-danger">{{ message }}</div>
                <div v-else="statusm == 1" class="message alert-success">{{ message }}</div>

                <button type="submit" class="btn btn-primary sub" v-on:click.prevent="editObject">Изменить</button>

            </div>
        </form>

    </div>
</template>

<script>
    export default {
        data: function(){
    		return {

				name: 		"",
				path:		"",
				img_format:	"",

				posterFile:	"",
				poster_name: "не выбран...",

				statusm: 0,
				message: "",
    		}
    	},

        mounted: function (){
            this.$nextTick(function(){

                let o = this;

                axios.get(o.$store.state.domain + '/pickings/' + o.$route.params.id).then(function(response){

                    o.name      	= response.data.name;
                    o.path   		= response.data.path;
                    o.img_format	= response.data.img_format;

                }).catch(function (error){
                    console.log(error);
                });

            })
        },

        methods: {

            editObject: function(){

                let o       = this;
                let data    = new FormData();

                data.append("name", o.name);
    			data.append("poster", o.posterFile);

                axios.post(o.$store.state.domain + '/pickings/' + o.$route.params.id + '/update', data, {
                    headers: {'Content-Type': 'multipart/form-data'}
                }).then(function(response){
                    if(response.data.response){

                        o.statusm = 1;
                        o.message = "Изменен!";


						axios.get(o.$store.state.domain + '/pickings/' + o.$route.params.id).then(function(response){

		                    o.name      	= response.data.name;
		                    o.path   		= response.data.path;
		                    o.img_format	= response.data.img_format;

		                }).catch(function (error){
		                    console.log(error);
		                });


                    }else{
                        o.statusm = -1;
                        o.message = response.data.err;
                    }

                }).catch(function (error){
                    console.log(error);
                    o.statusm = -1;
                    o.message = "Неизвестная ошибка!";
                })
            },

            posterFiles(){
                this.poster_name = this.$refs.poster.files[0].name;
    			this.posterFile = this.$refs.poster.files[0];
    		},

        }

    }
</script>
