<template>
    <div>
        <h2>Добавление продукта</h2>

        <form class="form" action="." method="post" accept-charset="utf-8">
            <div id="tab" class="demo">

                <div class="tabs">
                    <div class="tabs-head">
                        <div v-for="tab in tabs" :id="tab.id" :class="['tab', { active: tab.active }]" @click="changeTabs(tab.id)">{{ tab.name }}</div>
                    </div>
                    <div class="wrap-tab-content">

                        <div class="tab-content tab-1" v-show="currentTab == 1">

							<div class="name">Категория</div>
                			<div class="wrap_select_block">
                				<div class="select_block">
                					<div class="active_select form-control">{{ parent.activeSelectText }}</div>
                					<div class="change" @click="parent.activeSelect ? parent.activeSelect = false : parent.activeSelect = true"><i class="fas fa-sort-down"></i></div>
                				</div>
                				<div v-if="parent.activeSelect" class="select_elements">
                					<div v-for="obj in parent.objects" :key="obj.id" @click="changeParent(obj.id, obj.name)" class="select_element">{{ "—" . repeat(obj.level) + " " + obj.name }}</div>
                				</div>
                			</div>

							<div class="name">Производитель</div>
                			<div class="wrap_select_block">
                				<div class="select_block">
                					<div class="active_select form-control">{{ manufacturer.activeSelectText }}</div>
                					<div class="change" @click="manufacturer.activeSelect ? manufacturer.activeSelect = false : manufacturer.activeSelect = true"><i class="fas fa-sort-down"></i></div>
                				</div>
                				<div v-if="manufacturer.activeSelect" class="select_elements">
                					<div v-for="obj in manufacturer.objects" :key="obj.id" @click="changeManufacturer(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
                				</div>
                			</div>

							<div class="name">{{ stock.name }}</div>
							<div class="wrap_select_block">
								<div class="select_block">
									<div class="active_select form-control">{{ stock.text }}</div>
									<div class="change" @click="stock.active = stock.active ? false : true"><i class="fas fa-sort-down"></i></div>
								</div>
								<div v-if="stock.active" class="select_elements">
									<div :key="0" @click="changeStock(0, 'Без акции...')" class="select_element">Без акции...</div>
									<div v-for="obj in stock.objects" :key="obj.id" @click="changeStock(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
								</div>
							</div>

            				<label for='name'>Наименование</label>
            				<input v-model="name" id='name' class="form-control" name='name' type='text'/>

                            <label for='slug'>Url - адресс</label>
                            <input v-model="slug" id='slug' class="form-control" name='slug' type='text'/>

                            <div class="user-content">
                                <label for='content'>Контент</label>
                                <editor v-model="content" api-key='90qosixkylz14n41uwhlz2127dcmqb0xt9cvxx0wt2kfinzz' :init="this.$store.state.setting_editor"></editor>
                            </div>

            				<label for='price'>Цена</label>
            				<input v-model="price" id='price' class="form-control" name='price' type='number'/>

							<label for='old_price'>Старая цена</label>
            				<input v-model="old_price" id='old_price' class="form-control" name='old_price' type='number'/>

            				<label for='poster'>Постер</label>
            				<div class="inputPoster">
            					<input ref="poster" @change="posterFiles" type="file" name="poster" accept="image/jpeg,image/png" hidden />
                                <div class="image-name">{{ poster_name }}</div>
                                <div class="select-image" @click="$refs.poster.click()">Выбрать</div>
            				</div>

            				<label for='seo_title'>Seo Title</label>
            				<input v-model="seo_title" id='seo_title' class="form-control" name='seo_title' type='text'/>

            				<label for='seo_desc'>Seo Desc</label>
            				<input v-model="seo_desc" id='seo_desc' class="form-control" name='seo_desc' type='text'/>
            				<div class="checkbox">
            					<input v-model="market" id='market' name='market' type='checkbox'/>
            					<label for='market'>Маркет</label>
            				</div>
            				<div class="checkbox">
            					<input v-model="avalible" id='avalible' name='avalible' type='checkbox' />
            					<label for='avalible'>Доступность</label>
            				</div>
            			</div>

						<div class="tab-content tab-2" v-show="currentTab == 2">

							<div class="block-colors">
								<div class="wrap-color" v-for="color in colors">
									<div class="color-count">{{ color.images.length }}</div>
									<div
										:key="color.id"
										:class="['color', {'color-active': color.active}]"
										:style="{ background: getBack(color), backgroundSize: '100% 100%', backgroundRepeat: 'no-repeat'}"
										:title="color.name"
										@click="colorActivate(color.id)"
									></div>
								</div>
							</div>

							<div class="add-images">

	            				<button type="submit" v-on:click.prevent="changeFile" class="btn btn-success">Добавить изображения</button>
	            				<input ref="changefiles" @change="getBase64Image" type="file" name="images" accept="image/jpeg,image/png" multiple hidden />

	            				<div class="imagesBlock" v-for="color in colors" v-if="color.active">

	            					<div
	            						v-for="e in color.images"
	            						:key="e.id"
	            						:style="{backgroundImage: 'url(' + e.image + ')'}"
	            						:class="{main: e.status}"
	            						class="viewImages"
	            					>
	            						<span class="deleteImages" @click="deleteImages(e.id)">delete</span>
	            					</div>

	            				</div>

							</div>
            			</div>

						<div class="tab-content tab-3" v-show="currentTab == 3">

							<div class="wrap-characteristics">
								<div class="wrap-characteristic">
									<div class="name">Характеристики</div>
		                			<div class="wrap_select_block">
		                				<div class="select_block">
		                					<div class="active_select form-control">{{ characteristic.text }}</div>
		                					<div class="change" @click="characteristic.active = characteristic.active ? false : true"><i class="fas fa-sort-down"></i></div>
		                				</div>
		                				<div v-if="characteristic.active" class="select_elements">
		                					<div v-for="obj in characteristic.objects" :key="obj.id" @click="changeCharacteristic(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
		                				</div>
		                			</div>
								</div>
								<div class="wrap-characteristic">
									<div class="name">Значения</div>
		                			<div class="wrap_select_block">
		                				<div class="select_block">
		                					<div class="active_select form-control">{{ cvalue.text }}</div>
		                					<div class="change" @click="cvalue.active = cvalue.active ? false : true"><i class="fas fa-sort-down"></i></div>
		                				</div>
		                				<div v-if="cvalue.active" class="select_elements">
		                					<div v-for="obj in cvalue.objects" v-if="obj.parent == characteristic.object" :key="obj.id" @click="changeCvalue(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
		                				</div>
		                			</div>
								</div>
							</div>

							<div class="characteristics">

								<div class="characteristic" v-for="obj in characteristics" :key="obj.id">
									<div class="characteristic-name">{{ obj.characteristic.name }}</div>
									<div class="characteristic-val">{{ obj.cvalue.name }}</div>
									<div class="characteristic-del" @click="delCharacteristics(obj.id)"><i class="fas fa-minus"></i></div>
								</div>

							</div>

							<div class="addCharacteristics">
								<button type="submit" @click.prevent="addCharacteristic" class="btn btn-success">Добавить</button>
							</div>

						</div>

						<div class="tab-content tab-5" v-show="currentTab == 5">
							<div class="gifts">
								<div
								v-for="obj in pickings"
								:key="obj.id"
								:class="[obj.active ? 'gift-active' : 'gift']"
								@click="obj.active = obj.active ? false : true"
								>{{ obj.name }}</div>
							</div>
            			</div>

						<div class="tab-content tab-6" v-show="currentTab == 6">
							<div class="gifts">
								<div
								v-for="obj in options"
								:key="obj.id"
								:class="[obj.active ? 'gift-active' : 'gift']"
								@click="obj.active = obj.active ? false : true"
								>{{ obj.name }}</div>
							</div>
            			</div>

						<div class="tab-content tab-7" v-show="currentTab == 7">

							<div class="add-acces">

								<div class="wrap_select_block">
									<div class="select_block">
										<div class="active_select form-control">{{ accessorie.text }}</div>
										<div class="change" @click="accessorie.active = !accessorie.active"><i class="fas fa-sort-down"></i></div>
									</div>
									<div v-if="accessorie.active" class="select_elements">
										<div @click="changeAccessories(0, 'Выберите акссесуар...')" class="select_element">Выберите акссесуар...</div>
										<div v-for="obj in accessorie.objects" :key="obj.id" @click="changeAccessories(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
									</div>
								</div>

								<button type="submit" @click.prevent="addAcces" class="btn btn-success">Добавить</button>

							</div>

							<div class="accessories">
								<div v-for="obj in accessorie.activeObjects" :key="obj.id" class="accessorie">
									<div class='name'>{{ obj.name }}</div>
									<div class='delete-acces' @click="delAcces(obj.id)">отвязать</div>
								</div>
							</div>

            			</div>

                    </div>
                </div>

                <div v-if="statusm == 0" class="message">{{ message }}</div>
                <div v-else-if="statusm == -1" class="message alert-danger">{{ message }}</div>
                <div v-else="statusm == 1" class="message alert-success">{{ message }}</div>

                <button type="submit" class="btn btn-primary sub" v-on:click.prevent="addObject">Добавить продукт</button>

            </div>
        </form>
    </div>
</template>


<script>

    import Editor from '@tinymce/tinymce-vue';

    export default {

        data: function(){
    		return {

                currentTab: 1,
                tabs: [
                    {id: 1, name: 'Информация', active: true},
                    {id: 2, name: 'Изображения', active: false},
                    {id: 3, name: 'Характеристики', active: false},
					{id: 5, name: 'Комплектация', active: false},
					{id: 6, name: 'Опции', active: false},
					{id: 7, name: 'Акссесуары', active: false},
                ],

                parent: {
                    activeSelect: 		false,
                    activeSelectText: 	"Выберите категорию",
                    object:				"",
                    objects:			[],
                },

                manufacturer: {
                    activeSelect: 		false,
                    activeSelectText: 	"Выберите производителя",
                    object:				"",
                    objects:			[],
                },

				stock: {
					name:		"Выберите акцию",
                    active: 	false,
                    text: 		"Выберите акцию",
                    object:		0,
                    objects:	[],
                },

				colors: 	[],
				pickings:	[],
				options:	[],

				accessorie: {
					name: "Акссесуары",
					text: "Выберите акссесуар...",
					active: false,
					object: 0,
					objects: [],
					activeObjects: [],
				},


				characteristics: [],
				characteristic: {
					name: "Характеристики",
					text: "Выберите характеристику...",
					active: false,
					object: 0,
					objects: [],
				},

				cvalue: {
					name: "Значение",
					text: "Выберите значение...",
					active: false,
					object: 0,
					objects: [],
				},

                name: 		"",
                content: 	"",
                price: 		0,
				old_price:	0,
                slug: 		"",
                posterFile:	"",
                poster_name: "не выбран...",
                seo_title: 	"",
                seo_desc: 	"",
                market: 	false,
                avalible: 	true,

                statusm: 0,
                message: "",

    		}
    	},

        components: {
            'editor': Editor,
        },

        mounted: function(){
    		this.$nextTick(function(){

    			let o = this;

				//загружаем категории
    			axios.get(o.$store.state.domain + '/categories/get').then(function(response){
    				o.parent.objects = JSON.parse(response.data.objects);

					//загружаем производителей
					axios.get(o.$store.state.domain + '/manufacturers/get').then(function(response){
	    				o.manufacturer.objects = JSON.parse(response.data.objects);

						//загружаем цвета
						axios.get(o.$store.state.domain + '/colors/get').then(function(response){

							let temp = JSON.parse(response.data.objects);
							for(let i=0; i<temp.length; i++){
								temp[i]['active'] = false;
								temp[i]['images'] = [];
							}
							o.colors = temp;

							//загружаем акции
							axios.get(o.$store.state.domain + '/stocks/get').then(function(response){
			    				o.stock.objects = JSON.parse(response.data.objects);

								//загружаем комлектации
								axios.get(o.$store.state.domain + '/pickings/get').then(function(response){
				    				o.pickings = JSON.parse(response.data.objects);

									//загружаем опции
									axios.get(o.$store.state.domain + '/options/get').then(function(response){
					    				o.options = JSON.parse(response.data.objects);

										//загружаем характеристики
										axios.get(o.$store.state.domain + '/characteristics/get').then(function(response){
						    				o.characteristic.objects = JSON.parse(response.data.objects);

											//загружаем значение характеристик
											axios.get(o.$store.state.domain + '/cvalues/get').then(function(response){
							    				o.cvalue.objects = JSON.parse(response.data.objects);

												//загружаем продукты
												axios.get(o.$store.state.domain + '/products/get').then(function(response){
								    				o.accessorie.objects = JSON.parse(response.data.objects);
								    			}).catch(function (error){
								    				console.log(error);
								    			});

							    			}).catch(function (error){
							    				console.log(error);
							    			});

						    			}).catch(function (error){
						    				console.log(error);
						    			});

					    			}).catch(function (error){
					    				console.log(error);
					    			});

				    			}).catch(function (error){
				    				console.log(error);
				    			});

			    			}).catch(function (error){
			    				console.log(error);
			    			});

						}).catch(function (error){
							console.log(error);
						});
	    			}).catch(function (error){
	    				console.log(error);
	    			});
    			}).catch(function (error){
    				console.log(error);
    			});
    		})
    	},

    	methods: {

			delAcces: function(id){

				for(let i=0; i<this.accessorie.activeObjects.length; i++){
					if(this.accessorie.activeObjects[i].id == id){
						this.accessorie.objects.push(this.accessorie.activeObjects[i]);
						this.accessorie.activeObjects.splice(i, 1);
						break;
					}
				}

			},

			addAcces: function(){

				for(let i=0; i<this.accessorie.objects.length; i++){
					if(this.accessorie.objects[i].id == this.accessorie.object){

						this.accessorie.activeObjects.push(this.accessorie.objects[i]);
						this.accessorie.objects.splice(i, 1);

						this.accessorie.object 	= 0;
						this.accessorie.text 	= 'Выберите акссесуар...';

						break;
					}
				}

			},

			getBack: function(object){
				if(object.path == ""){
					return object.code;
				}
				return "url('" + this.$store.state.domain + '/' + object.path + "poster." + object.img_format + "')";
			},

			changeAccessories: function(id, name){
				this.accessorie.object 	= id;
				this.accessorie.active 	= false;
				this.accessorie.text 	= name;
			},

			changeStock: function(id, name){
				this.stock.object = id;
				this.stock.active = false;
				this.stock.text = name;
			},

			getBase64Image: function(e){

				let images 	= e.target.files;
				let o		= this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].active){
						for (let k=0; k<images.length; k++) {
							o.colors[i].images.push({'id': images[k].name, 'imageEncode': false, 'file': images[k], 'image': ''});
						}
					}
				}

				for(let i = 0; i<o.colors.length; i++){
					for(let k = 0; k<o.colors[i].images.length; k++){
						if(!o.colors[i].images[k].imageEncode){

							let formData = new FormData();
							formData.append("files", o.colors[i].images[k].file);

							axios.post(o.$store.state.domain + '/images/decode', formData, {
								headers: {'Content-Type': 'multipart/form-data'}
							}).then(function(response){

								for(let j=0; j<response.data.images.length; j++){
									o.colors[i].images[k].image = response.data.images[j].image;
									o.colors[i].images[k].imageEncode = true;
								}
							}).catch(function (error) {
								console.log(error);
							});

						}
					}
				}

			},

			delCharacteristics: function(id){
				let o = this;
				for(let i = 0; i<o.characteristics.length; i++){
					if(o.characteristics[i].id == id){
						o.characteristics.splice(i, 1);
					}
				}
			},

			//Добавляем характеристики
			addCharacteristic: function(){
				let o = this;

				let characteristicID = o.characteristic.object;
				let cvalueID = o.cvalue.object;

				let temp = {};
				for(let i=0; i<o.characteristic.objects.length; i++){
					if(o.characteristic.objects[i].id == characteristicID){
						temp['characteristic'] = o.characteristic.objects[i];
					}
				}

				if(temp['characteristic'] == undefined) return;

				for(let i=0; i<o.cvalue.objects.length; i++){
					if(o.cvalue.objects[i].id == cvalueID){
						temp['cvalue'] = o.cvalue.objects[i];
					}
				}
				if(temp['cvalue'] == undefined) return;

				temp['id'] = characteristicID + cvalueID;

				o.characteristics.push(temp);

			},


			//выбираем цвета
			colorActivate: function(id){
				let o = this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].id == id){
						o.colors[i].active = true;
					}else{
						o.colors[i].active = false;
					}
				}

			},

            //меняем табы
            changeTabs: function(id){
                this.currentTab = id;
                for(let i = 0; i<this.tabs.length; i++){
                    this.tabs[i].active = this.tabs[i].id == id ? true : false;
                }
            },

            //меняем категорию товара
            changeParent: function(id, name){
    			this.parent.object = id;
    			this.parent.activeSelect = false;
    			this.parent.activeSelectText = name;
    		},

			changeCharacteristic: function(id, name){
    			this.characteristic.object 	= id;
    			this.characteristic.active 	= false;
    			this.characteristic.text 	= name;
    		},

			changeCvalue: function(id, name){
    			this.cvalue.object 	= id;
    			this.cvalue.active 	= false;
    			this.cvalue.text 	= name;
    		},

            changeManufacturer: function(id, name){
    			this.manufacturer.object = id;
    			this.manufacturer.activeSelect = false;
    			this.manufacturer.activeSelectText = name;
    		},

    		addObject: function(){

    			let o    = this;
    			let data = new FormData();

                data.append("parent", o.parent.object);
                data.append("manufacturer", o.manufacturer.object);
                data.append("name", o.name);
    			data.append("content", o.content);
    			data.append("price", o.price);
				data.append("old_price", o.old_price);
    			data.append("slug", o.slug);
    			data.append("poster", o.posterFile);
    			data.append("seo_title", o.seo_title);
    			data.append("seo_desc", o.seo_desc);
    			data.append("market", o.market);
    			data.append("avalible", o.avalible);
				data.append("stocksID", o.stock.object);

    			axios.post(o.$store.state.domain + window.location.pathname, data, {
    				headers: {'Content-Type': 'multipart/form-data'}
			    }).then(function(response){

    				if(response.data.response){

						o.statusm = 1;
    					o.message = "Основная информация добавлена...";

						let productID = response.data.id;

						//добавляем комлектацию
						for(let i=0; i<o.pickings.length; i++){
							if(o.pickings[i].active){

								data = new FormData();
								data.append("pid", productID);
								data.append("pic_id", o.pickings[i].id);

								axios.post(o.$store.state.domain + "/paps/add", data).then(function(response){
									o.message = response.data.response ? "Добавил комплектацию: " + o.pickings[i].name : "Возникла ошибка комплектации.";
								}).catch(function (error){
				                    console.log(error);
				    				o.statusm = -1;
				    				o.message = "Неизвестная ошибка!";
				    			})
							}
						}

						//добавляем опции
						for(let i=0; i<o.options.length; i++){
							if(o.options[i].active){

								data = new FormData();
								data.append("pid", productID);
								data.append("oid", o.options[i].id);

								axios.post(o.$store.state.domain + "/oaps/add", data).then(function(response){
									o.message = response.data.response ? "Добавил: " : "Возникла ошибка: ";
									o.message += "options";
								}).catch(function (error){
				                    console.log(error);
				    				o.statusm = -1;
				    				o.message = "Неизвестная ошибка!";
				    			})
							}
						}

						//добавляем к цветам изображения
						for(let i=0; i<o.colors.length; i++){
							if(o.colors[i].images.length){

								for(let k=0; k<o.colors[i].images.length; k++){

									let data_images = new FormData();

									data_images.append("productID", productID);
									data_images.append("colorID", o.colors[i].id);
									data_images.append("poster", o.colors[i].images[k].file);

									axios.post(o.$store.state.domain + "/images/add", data_images, {
					    				headers: {'Content-Type': 'multipart/form-data'}
								    }).then(function(response){
										o.message = response.data.response ? "Обработал цвета: " + o.colors[i].name : "Возникла ошибка у цвета: " + o.colors[i].name;
									}).catch(function (error){
					                    console.log(error);
					    				o.statusm = -1;
					    				o.message = "Неизвестная ошибка!";
					    			});

								}

							}
						}


						//добавляем значение характеристик
						if(o.characteristics.length){
							for(let i=0; i<o.characteristics.length; i++){

								data = new FormData();
								data.append("pid", productID);
								data.append("cid", o.characteristics[i].id);

								axios.post(o.$store.state.domain + "/caps/add", data).then(function(response){
									o.message = response.data.response ? "Добавил: " : "Возникла ошибка: ";
									o.message += "caps";
								}).catch(function (error){
									console.log(error);
									o.statusm = -1;
									o.message = "Неизвестная ошибка!";
								});

							}
						}

						//добавляем аксессуары
						if(o.accessorie.activeObjects){
							for(let i=0; i<o.accessorie.activeObjects.length; i++){

								data = new FormData();
								data.append("parent", productID);
								data.append("pid", o.accessorie.activeObjects[i].id);

								axios.post(o.$store.state.domain + "/accessories/add", data).then(function(response){
									o.message = response.data.response ? "Добавил: " : "Возникла ошибка: ";
									o.message += "accessories";
								}).catch(function (error){
									console.log(error);
									o.statusm = -1;
									o.message = "Неизвестная ошибка!";
								});

							}
						}


    				}else{
    					o.statusm = -1;
    					o.message = response.data.err;
    				}

    			}).catch(function (error){
                    console.log(error);
    				o.statusm = -1;
    				o.message = "Неизвестная ошибка!";
    			})

    		},

            posterFiles(){
                this.poster_name = this.$refs.poster.files[0].name;
    			this.posterFile = this.$refs.poster.files[0];
    		},

    		deleteImages: function(id){
				let o = this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].active){
						for(let k = 0; k<o.colors[i].images.length; k++){
							if(o.colors[i].images[k].id == id){
								o.colors[i].images.splice(i, 1);
								break;
							}
						}
					}
				}

    		},

			changeFile: function(){
				let o = this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].active){
						const elem = this.$refs.changefiles;
		    			elem.click()
						return
					}
				}
				o.statusm = -1;
				o.message = "Выберите цвет!";

    		},


    	}

    }
</script>
