									></div>
								</div>
							</div>

							<div class="add-images">

	            				<button type="submit" v-on:click.prevent="changeFile" class="btn btn-success">Добавить изображения</button>
	            				<input ref="changefiles" @change="getBase64Image" type="file" name="images" accept="image/jpeg,image/png" multiple hidden />

	            				<div class="imagesBlock" v-for="color in colors" v-if="color.active">

	            					<div
										v-for="e in color.images"
	            						:key="e.id"
	            						:style="{backgroundImage: 'url(' + setImage(e) + ')'}"
	            						class="viewImages"
	            					>
	            						<span class="deleteImages" @click="deleteImages(e.id)">delete</span>
	            					</div>
	            				</div>

							</div>
            			</div>

						<div class="tab-content tab-3" v-show="currentTab == 3">

							<div class="wrap-characteristics">
								<div class="wrap-characteristic">
									<div class="name">Характеристики</div>
									<div class="wrap_select_block">
										<div class="select_block">
											<div class="active_select form-control">{{ characteristic.text }}</div>
											<div class="change" @click="characteristic.active = characteristic.active ? false : true"><i class="fas fa-sort-down"></i></div>
										</div>
										<div v-if="characteristic.active" class="select_elements">
											<div v-for="obj in characteristic.objects" :key="obj.id" @click="changeCharacteristic(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
										</div>
									</div>
								</div>
								<div class="wrap-characteristic">
									<div class="name">Значения</div>
									<div class="wrap_select_block">
										<div class="select_block">
											<div class="active_select form-control">{{ cvalue.text }}</div>
											<div class="change" @click="cvalue.active = cvalue.active ? false : true"><i class="fas fa-sort-down"></i></div>
										</div>
										<div v-if="cvalue.active" class="select_elements">
											<div v-for="obj in cvalue.objects" v-if="obj.parent == characteristic.object" :key="obj.id" @click="changeCvalue(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
										</div>
									</div>
								</div>
							</div>

							<div class="characteristics">

								<div class="characteristic" v-for="obj in characteristics" :key="obj.id">
									<div class="characteristic-name">{{ obj.characteristic.name }}</div>
									<div class="characteristic-val">{{ obj.cvalue.name }}</div>
									<div class="characteristic-del" @click="delCharacteristics(obj.id)"><i class="fas fa-minus"></i></div>
								</div>

							</div>

							<div class="addCharacteristics">
								<button type="submit" @click.prevent="addCharacteristic" class="btn btn-success">Добавить</button>
							</div>

						</div>

						<div class="tab-content tab-5" v-show="currentTab == 5">

							<div class="gifts">
								<div
								v-for="obj in pickings"
								:key="obj.id"
								:class="[obj.active ? 'gift-active' : 'gift']"
								@click="obj.active = obj.active ? false : true"
								>{{ obj.name }}</div>
							</div>

						</div>

						<div class="tab-content tab-6" v-show="currentTab == 6">
							<div class="gifts">
								<div
								v-for="obj in options"
								:key="obj.id"
								:class="[obj.active ? 'gift-active' : 'gift']"
								@click="obj.active = obj.active ? false : true"
								>{{ obj.name }}</div>
							</div>
						</div>

                    </div>
                </div>

				<div class="tab-content tab-7" v-show="currentTab == 7">

					<div class="add-acces">

						<div class="wrap_select_block">
							<div class="select_block">
								<div class="active_select form-control">{{ accessorie.text }}</div>
								<div class="change" @click="accessorie.active = !accessorie.active"><i class="fas fa-sort-down"></i></div>
							</div>
							<div v-if="accessorie.active" class="select_elements">
								<div @click="changeAccessories(0, 'Выберите акссесуар...')" class="select_element">Выберите акссесуар...</div>
								<div v-for="obj in accessorie.objects" :key="obj.id" @click="changeAccessories(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
							</div>
						</div>

						<button type="submit" @click.prevent="addAcces" class="btn btn-success">Добавить</button>

					</div>

					<div class="accessories">
						<div v-for="obj in accessorie.activeObjects" :key="obj.id" class="accessorie">
							<div class='name'>{{ obj.name }}</div>
							<div class='delete-acces' @click="delAcces(obj.id)">отвязать</div>
						</div>
					</div>

				</div>


                <div v-if="statusm == 0" class="message">{{ message }}</div>
                <div v-else-if="statusm == -1" class="message alert-danger">{{ message }}</div>
                <div v-else="statusm == 1" class="message alert-success">{{ message }}</div>

                <button type="submit" class="btn btn-primary sub" v-on:click.prevent="editObject">Изменить</button>

            </div>
