    </div>
</template>


<script>

    import Editor from '@tinymce/tinymce-vue';

    export default {
        data: function(){
    		return {
				currentTab: 1,
                tabs: [
                    {id: 1, name: 'Информация', active: true},
                    {id: 2, name: 'Изображения', active: false},
                    {id: 3, name: 'Характеристики', active: false},
					{id: 5, name: 'Комплектация', active: false},
					{id: 6, name: 'Опции', active: false},
					{id: 7, name: 'Акссесуары', active: false},
                ],

                parent: {
                    activeSelect: 		false,
                    activeSelectText: 	"Выберите меню",
                    object:				"",
                    objects:			[],
                },

                manufacturer: {
                    activeSelect: 		false,
                    activeSelectText: 	"Выберите производителя",
                    object:				"",
                    objects:			[],
                },

				stock: {
					name:		"Выберите акцию",
                    active: 	false,
                    text: 		"Выберите акцию",
                    object:		0,
                    objects:	[],
                },

				accessorie: {
					name: "Акссесуары",
					text: "Выберите акссесуар...",
					active: false,
					object: 0,
					objects: [],
					activeObjects: [],
					assocObjects: [],
				},

				colors: 	[],
				images:		[],
				pickings:	[],
				paps:		[],
				options:	[],
				oaps:		[],

				characteristics: [],
				characteristic: {
					name: "Характеристики",
					text: "Выберите характеристику",
					active: false,
					object: 0,
					objects: [],
				},

				cvalue: {
					name: "Выберите значение",
					text: "Выберите значение",
					active: false,
					object: 0,
					objects: [],
				},

				caps:		[],

                name: 		"",
                content: 	"",
                price: 		0,
				old_price:	0,
                slug: 		"",
				path:		"",
				poster:		"",
				img_format:	"",
                posterFile:	"",
                poster_name: "не выбран...",
                seo_title: 	"",
                seo_desc: 	"",
                market: 	false,
                avalible: 	true,
				stocksID:	0,

                statusm: 0,
                message: "",
    		}
    	},

        components: {
            'editor': Editor,
        },

        mounted: function (){
            this.$nextTick(function(){

                let o = this;

				//загружаем категории
                axios.get(o.$store.state.domain + '/categories/get').then(function(response){
    				o.parent.objects = JSON.parse(response.data.objects);

					//загружаем производителей
					axios.get(o.$store.state.domain + '/manufacturers/get').then(function(response){
	    				o.manufacturer.objects = JSON.parse(response.data.objects);

						//загружаем комлектации
						axios.get(o.$store.state.domain + '/pickings/get').then(function(response){
							o.pickings = JSON.parse(response.data.objects);

							//загружаем ассоциации с комлектациями
							axios.get(o.$store.state.domain + '/paps/' + o.$route.params.id).then(function(response){
								o.paps = JSON.parse(response.data.objects);

								//инициализируем комлектации
								for(let i=0; i<o.pickings.length; i++){
									for(let k=0; k<o.paps.length; k++){
										if(o.pickings[i].id == o.paps[k].pic_id){
											o.pickings[i].active = true;
										}
									}
								}

								//загружаем цвета
								axios.get(o.$store.state.domain + '/colors/get').then(function(response){
									let temp = JSON.parse(response.data.objects);

									for(let i=0; i<temp.length; i++){
										temp[i]['active'] = false;
										temp[i]['images'] = [];
									}
				    				o.colors = temp;

									//загружаем изображения к цветам
									axios.get(o.$store.state.domain + '/images/' + o.$route.params.id).then(function(response){
										o.images = JSON.parse(response.data.objects);

										for(let i=0; i < o.colors.length; i++){
											for(let k=0; k < o.images.length; k++){
												if(o.colors[i].id == o.images[k].colorID){
													o.colors[i].images.push({
														'id': o.images[k].id,
														'imageEncode': false,
														'file': false,
														'image': o.images[k],
														'serverImage': true,
													});
												}
											}
										}

										//загружаем информацию о продукте
										axios.get(o.$store.state.domain + '/products/' + o.$route.params.id).then(function(response){

						                    for(let i=0; i<o.parent.objects.length; i++){
						                        if(o.parent.objects[i].id == response.data.parent){
						                            o.parent.object = o.parent.objects[i].id;
						                            o.parent.activeSelectText = o.parent.objects[i].name;
						                            break;
						                        }
						                    }

						                    for(let i=0; i<o.manufacturer.objects.length; i++){
						                        if(o.manufacturer.objects[i].id == response.data.manufacturer){
						                            o.manufacturer.object = o.manufacturer.objects[i].id;
						                            o.manufacturer.activeSelectText = o.manufacturer.objects[i].name;
						                            break;
						                        }
						                    }

						                    o.name      = response.data.name;
						                    o.content   = response.data.content;
						                    o.price     = response.data.price;
											o.old_price = response.data.old_price;
						                    o.slug      = response.data.slug;
						                    o.seo_title = response.data.seo_title;
						                    o.seo_desc  = response.data.seo_desc;
						                    o.poster    = response.data.poster;
						                    o.img_format = response.data.img_format;
						                    o.market    = response.data.market;
						                    o.avalible  = response.data.avalible;
											o.path		= o.$store.state.domain + '/' + response.data.poster + "poster." + response.data.img_format;
											o.stocksID	= response.data.stocksID;


											//загружаем опции
											axios.get(o.$store.state.domain + '/options/get').then(function(response){
												o.options = JSON.parse(response.data.objects);

												//загружаем ассоциации с опции
												axios.get(o.$store.state.domain + '/oaps/' + o.$route.params.id).then(function(response){
													o.oaps = JSON.parse(response.data.objects);

													//инициализируем опции
													for(let i=0; i<o.options.length; i++){
														for(let k=0; k<o.oaps.length; k++){
															if(o.options[i].id == o.oaps[k].oid){
																o.options[i].active = true;
															}
														}
													}

													//загружаем характеристики
													axios.get(o.$store.state.domain + '/characteristics/get').then(function(response){
									    				o.characteristic.objects = JSON.parse(response.data.objects);

														//загружаем значение характеристик
														axios.get(o.$store.state.domain + '/cvalues/get').then(function(response){
										    				o.cvalue.objects = JSON.parse(response.data.objects);

															//загружаем значение характеристик
															axios.get(o.$store.state.domain + '/caps/' + o.$route.params.id ).then(function(response){
											    				o.caps = JSON.parse(response.data.objects);

																//инициализируем характеристики
																for(let i=0; i<o.characteristic.objects.length; i++){
																	for(let k=0; k<o.cvalue.objects.length; k++){
																		for(let j=0; j<o.caps.length; j++){
																			if(o.characteristic.objects[i].id == o.cvalue.objects[k].parent && o.cvalue.objects[k].id == o.caps[j].cid){
																				o.characteristics.push({
																					'server_id': o.caps[j].id,
																					'characteristic': o.characteristic.objects[i],
																					'cvalue': o.cvalue.objects[k],
																					'id': o.characteristic.objects[i].id + o.cvalue.objects[k].id,
																				});
																			}
																		}
																	}
																}

																//загружаем акции
																axios.get(o.$store.state.domain + '/stocks/get').then(function(response){
																	o.stock.objects = JSON.parse(response.data.objects);
																	for(let i=0; i<o.stock.objects.length; i++){
																		if(o.stock.objects[i].id == o.stocksID){
																			o.stock.object = o.stock.objects[i].id;
																			o.stock.text = o.stock.objects[i].name;
																		}
																	}

																	//загружаем продукты
																	axios.get(o.$store.state.domain + '/products/get').then(function(response){
													    				o.accessorie.objects = JSON.parse(response.data.objects);

																		//загружаем продукты
																		axios.get(o.$store.state.domain + '/accessories/' + o.$route.params.id).then(function(response){
																			o.accessorie.assocObjects = JSON.parse(response.data.objects);

																			//удаляем редактируемый товар из списка
																			for(let i=0; i<o.accessorie.objects.length; i++){
																				if(o.accessorie.objects[i].id == o.$route.params.id){
																					o.accessorie.objects.splice(i, 1);
																				}
																			}

																			//добавляем товары в активный список, удаляем товары из общего списка
																			for(let k=0; k<o.accessorie.assocObjects.length; k++){
																				for(let i=0; i<o.accessorie.objects.length; i++){
																					if(o.accessorie.objects[i].id == o.accessorie.assocObjects[k].pid){
																						o.accessorie.activeObjects.push(o.accessorie.objects[i]);
																						o.accessorie.objects.splice(i, 1);
																					}
																				}
																			}

																		}).catch(function (error){
																			console.log(error);
																		});

													    			}).catch(function (error){
													    				console.log(error);
													    			});

																}).catch(function (error){
																	console.log(error);
																});

											    			}).catch(function (error){
											    				console.log(error);
											    			});


										    			}).catch(function (error){
										    				console.log(error);
										    			});

									    			}).catch(function (error){
									    				console.log(error);
									    			});


												}).catch(function (error){
								                    console.log(error);
								                });

											}).catch(function (error){
							                    console.log(error);
							                });

						                }).catch(function (error){
						                    console.log(error);
						                });

									}).catch(function (error){
										console.log(error);
									});

								}).catch(function (error){
									console.log(error);
								});

							}).catch(function (error){
								console.log(error);
							});

						}).catch(function (error){
							console.log(error);
						});

	    			}).catch(function (error){
	    				console.log(error);
	    			});

    			}).catch(function (error){
    				console.log(error);
    			});

            })
        },

        methods: {

			delAcces: function(id){

				for(let i=0; i<this.accessorie.activeObjects.length; i++){
					if(this.accessorie.activeObjects[i].id == id){
						this.accessorie.objects.push(this.accessorie.activeObjects[i]);
						this.accessorie.activeObjects.splice(i, 1);
						break;
					}
				}

			},

			addAcces: function(){

				for(let i=0; i<this.accessorie.objects.length; i++){
					if(this.accessorie.objects[i].id == this.accessorie.object){

						this.accessorie.activeObjects.push(this.accessorie.objects[i]);
						this.accessorie.objects.splice(i, 1);

						this.accessorie.object 	= 0;
						this.accessorie.text 	= 'Выберите акссесуар...';

						break;
					}
				}

			},

			changeAccessories: function(id, name){
				this.accessorie.object 	= id;
				this.accessorie.active 	= false;
				this.accessorie.text 	= name;
			},

			getBack: function(object){
				if(object.path == ""){
					return object.code;
				}
				return "url('" + this.$store.state.domain + '/' + object.path + "poster." + object.img_format + "')";
			},

			changeStock: function(id, name){
				this.stock.object = id;
				this.stock.active = false;
				this.stock.text = name;
			},

			changeCharacteristic: function(id, name){
    			this.characteristic.object 	= id;
    			this.characteristic.active 	= false;
    			this.characteristic.text 	= name;

				this.cvalue.object 	= 0;
				this.cvalue.text 	= this.cvalue.name;
    		},

			changeCvalue: function(id, name){
    			this.cvalue.object 	= id;
    			this.cvalue.active 	= false;
    			this.cvalue.text 	= name;
    		},

			delCharacteristics: function(id){
				let o = this;
				for(let i = 0; i<o.characteristics.length; i++){
					if(o.characteristics[i].id == id){

						if(o.characteristics[i].server_id != undefined){
			    			let formData = new FormData();
			    			formData.append("id", o.characteristics[i].server_id);

			    			axios.post(o.$store.state.domain + '/caps/delete', formData).then(function(response){
			    				o.characteristics.splice(i, 1);
			    			}).catch(function (error) {
			    				console.log(error);
			    			});
						}

					}
				}
			},

			//Добавляем характеристики
			addCharacteristic: function(){
				let o = this;

				let characteristicID = o.characteristic.object;
				let cvalueID = o.cvalue.object;

				let temp = {};
				for(let i=0; i<o.characteristic.objects.length; i++){
					if(o.characteristic.objects[i].id == characteristicID){
						temp['characteristic'] = o.characteristic.objects[i];
					}
				}

				if(temp['characteristic'] == undefined) return;

				for(let i=0; i<o.cvalue.objects.length; i++){
					if(o.cvalue.objects[i].id == cvalueID){
						temp['cvalue'] = o.cvalue.objects[i];
					}
				}
				if(temp['cvalue'] == undefined) return;

				temp['id'] = characteristicID + cvalueID;

				o.characteristics.push(temp);

			},

			setImage: function(e){

				if(e.serverImage && !e.imageEncode){
					return this.$store.state.domain + '/' + e.image.name + '.' + e.image.img_format
				}else{
					return e.image
				}

			},

			//выбираем цвета
			colorActivate: function(id){
				let o = this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].id == id){
						o.colors[i].active = true;
					}else{
						o.colors[i].active = false;
					}
				}
			},

            //меняем табы
            changeTabs: function(id){
                this.currentTab = id;
                for(let i = 0; i<this.tabs.length; i++){
                    this.tabs[i].active = this.tabs[i].id == id ? true : false;
                }
            },

            //меняем категорию товара
            changeParent: function(id, name){
                this.parent.object = id;
                this.parent.activeSelect = false;
                this.parent.activeSelectText = name;
            },

            changeManufacturer: function(id, name){
    			this.manufacturer.object = id;
    			this.manufacturer.activeSelect = false;
    			this.manufacturer.activeSelectText = name;
    		},

            editObject: function(){

                let o       = this;
                let data    = new FormData();

                data.append("parent", o.parent.object);
                data.append("manufacturer", o.manufacturer.object);
                data.append("name", o.name);
    			data.append("content", o.content);
    			data.append("price", o.price);
				data.append("old_price", o.old_price);
    			data.append("slug", o.slug);
    			data.append("poster", o.posterFile);
    			data.append("seo_title", o.seo_title);
    			data.append("seo_desc", o.seo_desc);
    			data.append("market", o.market);
    			data.append("avalible", o.avalible);
				data.append("stocksID", o.stock.object);

                axios.post(o.$store.state.domain + '/products/' + o.$route.params.id + '/update', data, {
                    headers: {
                      'Content-Type': 'multipart/form-data'
                    }
                }).then(function(response){
                    if(response.data.response){

                        o.statusm = 1;
                        o.message = "Изменен!";

						let productID = o.$route.params.id;

						//удаляем все связи с опциями
						data = new FormData();
						data.append("pid", productID);
						axios.post(o.$store.state.domain + "/oaps/delete", data).then(function(response){

							//добавляем опции к продукту
							for(let i=0; i<o.options.length; i++){
								if(o.options[i].active){
									data = new FormData();
									data.append("pid", productID);
									data.append("oid", o.options[i].id);

									axios.post(o.$store.state.domain + "/oaps/add", data).then(function(response){
										o.message = response.data.response ? "Добавил: ": "Возникла ошибка: ";
										o.message += "опции";
									}).catch(function (error){
										console.log(error);
										o.statusm = -1;
										o.message = "Неизвестная ошибка!";
									})
								}
							}

							//загружаем ассоциации с опциями
							axios.get(o.$store.state.domain + '/oaps/' + o.$route.params.id).then(function(response){
								o.oaps = JSON.parse(response.data.objects);

								//инициализируем опции
								for(let i=0; i<o.options.length; i++){
									for(let k=0; k<o.oaps.length; k++){
										if(o.options[i].id == o.oaps[k].oid){
											o.options[i].active = true;
										}
									}
								}

							}).catch(function (error){
								console.log(error);
							})
						}).catch(function (error){
							console.log(error);
						})

						//удаляем все связи с комплектацией
						data = new FormData();
						data.append("pid", productID);
						axios.post(o.$store.state.domain + "/paps/delete", data).then(function(response){

								//добавляем комплектации
								for(let i=0; i<o.pickings.length; i++){
									if(o.pickings[i].active){
										data = new FormData();
										data.append("pid", productID);
										data.append("pic_id", o.pickings[i].id);

										axios.post(o.$store.state.domain + "/paps/add", data).then(function(response){
											o.message = response.data.response ? "Добавил" : "Возникла ошибка.";
										}).catch(function (error){
											console.log(error);
											o.statusm = -1;
											o.message = "Неизвестная ошибка!";
										})
									}
								}

								//загружаем ассоциации с комлектациями
								axios.get(o.$store.state.domain + '/paps/' + o.$route.params.id).then(function(response){
									o.paps = JSON.parse(response.data.objects);

									//инициализируем комлектации
									for(let i=0; i<o.pickings.length; i++){
										for(let k=0; k<o.paps.length; k++){
											if(o.pickings[i].id == o.paps[k].pic_id){
												o.pickings[i].active = true;
											}
										}
									}

								}).catch(function (error){
									console.log(error);
								})

						}).catch(function (error){
							console.log(error);
							o.statusm = -1;
							o.message = "Неизвестная ошибка!";
						})

						//добавляем изображения
						for(let i=0; i<o.colors.length; i++){
							if(o.colors[i].images.length){
								for(let k=0; k<o.colors[i].images.length; k++){
									if(!o.colors[i].images[k].serverImage){

										let data_images = new FormData();
										data_images.append("productID", o.$route.params.id);
										data_images.append("colorID", o.colors[i].id);
										data_images.append("poster", o.colors[i].images[k].file);

										axios.post(o.$store.state.domain + "/images/add", data_images, {
						    				headers: {'Content-Type': 'multipart/form-data'}
									    }).then(function(response){
											o.colors[i].images[k].serverImage = true;
											o.message = response.data.response ? "Обработал цвета: " + o.colors[i].name : "Возникла ошибка у цвета: " + o.colors[i].name;
										}).catch(function (error){
						                    console.log(error);
						    				o.statusm = -1;
						    				o.message = "Неизвестная ошибка!";
						    			});
									}
								}
							}
						}

						//добавляем значение характеристик
						if(o.characteristics.length){
							for(let i=0; i<o.characteristics.length; i++){

								data = new FormData();
								data.append("pid", productID);
								data.append("cid", o.characteristics[i].cvalue.id);

								axios.post(o.$store.state.domain + "/caps/add", data).then(function(response){
									o.message = response.data.response ? "Добавил: " : "Возникла ошибка: ";
									o.message += "caps";
								}).catch(function (error){
									console.log(error);
									o.statusm = -1;
									o.message = "Неизвестная ошибка!";
								});

							}
						}

						//удаляем все связи с другими товарами
						data = new FormData();
						data.append("parent", productID);
						axios.post(o.$store.state.domain + "/accessories/delete", data).then(function(response){

							//добавляем аксессуары
							if(o.accessorie.activeObjects){
								for(let i=0; i<o.accessorie.activeObjects.length; i++){

									data = new FormData();
									data.append("parent", productID);
									data.append("pid", o.accessorie.activeObjects[i].id);

									axios.post(o.$store.state.domain + "/accessories/add", data).then(function(response){
										o.message = response.data.response ? "Добавил: " : "Возникла ошибка: ";
										o.message += "accessories";
									}).catch(function (error){
										console.log(error);
										o.statusm = -1;
										o.message = "Неизвестная ошибка!";
									});

								}
							}
						
						}).catch(function (error){
							console.log(error);
							o.statusm = -1;
							o.message = "Неизвестная ошибка!";
						})


                    }else{
                        o.statusm = -1;
                        o.message = response.data.err;
                    }

                }).catch(function (error){
                    console.log(error);
                    o.statusm = -1;
                    o.message = "Неизвестная ошибка!";
                })
            },

            posterFiles(){
                this.poster_name = this.$refs.poster.files[0].name;
    			this.posterFile = this.$refs.poster.files[0];
    		},

			deleteImages: function(id){

				let o = this;

				axios.post(o.$store.state.domain + '/images/delete/' + id).then(function(response){
					for(let i = 0; i<o.colors.length; i++){
						for(let k = 0; k<o.colors[i].images.length; k++){
							if(o.colors[i].images[k].id == id){
								o.colors[i].images.splice(k, 1);
								break;
							}
						}
					}
    			}).catch(function (error) {
    				console.log(error);
    			});

    		},

			changeFile: function(){
				let o = this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].active){
						const elem = this.$refs.changefiles;
		    			elem.click()
						return
					}
				}
				o.statusm = -1;
				o.message = "Выберите цвет!";

    		},

			getBase64Image: function(e){

				let images 	= e.target.files;
				let o		= this;

				for(let i = 0; i<o.colors.length; i++){
					if(o.colors[i].active){
						for (let k=0; k<images.length; k++) {
							o.colors[i].images.push({'id': images[k].name, 'imageEncode': false, 'file': images[k], 'image': ''});
						}
					}
				}


				for(let i = 0; i<o.colors.length; i++){
					for(let k = 0; k<o.colors[i].images.length; k++){
						if(!o.colors[i].images[k].imageEncode && !o.colors[i].images[k].serverImage){

							let formData = new FormData();
							formData.append("files", o.colors[i].images[k].file);

							axios.post(o.$store.state.domain + '/images/decode', formData, {
								headers: {'Content-Type': 'multipart/form-data'}
							}).then(function(response){

								for(let j=0; j<response.data.images.length; j++){
									o.colors[i].images[k].image = response.data.images[j].image;
									o.colors[i].images[k].imageEncode = true;
								}
							}).catch(function (error) {
								console.log(error);
							});

						}
					}
				}

			},

        }

    }
</script>
