<template>
    <div>
        <h2>Добавление артикля</h2>

        <form class="form" action="." method="post" accept-charset="utf-8">
            <div id="tab" class="demo">

				<div class="name">{{ product.name }}</div>
				<div class="wrap_select_block">
					<div class="select_block">
						<div class="active_select form-control">{{ product.text }}</div>
						<div class="change" @click="product.active = product.active ? false : true"><i class="fas fa-sort-down"></i></div>
					</div>
					<div v-if="product.active" class="select_elements">
						<div :key="0" @click="changeProduct(0, 'Родитель')" class="select_element">Родитель</div>
						<div v-for="obj in product.objects" :key="obj.id" @click="changeProduct(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
					</div>
				</div>

				<div class="name">{{ color.name }}</div>
				<div class="wrap_select_block">
					<div class="select_block">
						<div class="active_select form-control">{{ color.text }}</div>
						<div class="change" @click="color.active = color.active ? false : true"><i class="fas fa-sort-down"></i></div>
					</div>
					<div v-if="color.active" class="select_elements">
						<div :key="0" @click="changeColor(0, 'Без акции...')" class="select_element">Без акции...</div>
						<div v-for="obj in color.objects" :key="obj.id" @click="changeColor(obj.id, obj.name)" class="select_element">{{ obj.name }}</div>
					</div>
				</div>

    			<label for='name'>Наименование</label>
    			<input v-model="name" id='name' class="form-control" name='name' type='text'/>

                <div v-if="statusm == 0" class="message">{{ message }}</div>
                <div v-else-if="statusm == -1" class="message alert-danger">{{ message }}</div>
                <div v-else="statusm == 1" class="message alert-success">{{ message }}</div>

                <button type="submit" class="btn btn-primary sub" v-on:click.prevent="addObject">Добавить</button>
            </div>
        </form>
    </div>
</template>


<script>

    export default {
        data: function(){
    		return {

				product: {
					name:		"Выберите продукт",
					active: 	false,
					text: 		"Выберите продукт",
					object:		0,
					objects:	[],
				},

				color: {
					name:		"Выберите цвет",
					active: 	false,
					text: 		"Выберите цвет",
					object:		0,
					objects:	[],
				},

        		name: 				"",

        		statusm: 			0,
        		message:			"",

    		}
    	},

		mounted: function (){
			this.$nextTick(function(){

				let o = this;

				axios.get(o.$store.state.domain + '/products/get').then(function(response){
					o.product.objects = JSON.parse(response.data.objects);

					axios.get(o.$store.state.domain + '/colors/get').then(function(response){
	    				o.color.objects = JSON.parse(response.data.objects);

	    			}).catch(function(error){
	    				console.log(error);
	    			});


				}).catch(function(error){
					console.log(error);
				});

			})
		},

        methods: {

			changeProduct: function(id, name){
				this.product.object = id;
				this.product.active = false;
				this.product.text = name;
			},

			changeColor: function(id, name){
				this.color.object = id;
				this.color.active = false;
				this.color.text = name;
			},

    		addObject: function(){

    			let o = this;
    			let data = new FormData();

    			data.append("name", this.name);
    			data.append("pds_id", this.product.object);
    			data.append("cls_id", this.color.object);

    			axios.post(o.$store.state.domain + window.location.pathname, data).then(function(response){

    				if(response.data.response){
    					o.statusm          = 1;
    					o.message          = "Добавлен!";
    				}else{
    					o.statusm 	= -1;
    					o.message	= response.data.err;
    				}

    			}).catch(function(error){
                    console.log(error);
    				o.statusm 	= -1;
    				o.message	= "Неизвестная ошибка!";
    			})

    		}

    	}

    }
</script>
